<!--suppress HtmlUnknownTag, HtmlUnknownAttribute, CheckEmptyScriptTag -->
<template>
  <v-container fluid>
    <h2>Graful expresiilor</h2>

    <VisWrapper v-if="!!graphData"
                :data="graphData" />

  </v-container>
</template>

<script>
  import AxiosHelper from '../components/AxiosHelper.vue';
  import Notifications from '../components/Notifications.vue';
  import VisWrapper from '../components/VisWrapper.vue';

  /* Sub-executive TODO
   * [ ] Subgraphs, obvs.
   * [ ] Copy button with orig., lit., meaning, all options
   * [ ]
   * [ ]
   */

  function blockLinesToExpression(lines) {
    if (lines.length < 3) {
      console.warn('Incomplete block!\n', lines.join('\n'));
      return [];
    }

    return [{
      phrase: lines[0],
      meaning: lines[1],
      lit: lines[2],
      related: lines.slice(3).map(r => r.replace(/ ?-> ?/, ''))
    }];
  }

  export default {
    components: {Notifications, VisWrapper},
    mixins: [AxiosHelper],

    data: () => ({
      graphData: null
    }),

    mounted () {
      this.plainAxios('fetching expressions', axios => axios.get(this.$dataUrl + '/expressions/expressions.txt'),
        data => this.graphData = data
          .trim()
          .split(/\n{2,}/)
          .flatMap(block => blockLinesToExpression(block.split('\n'))));
    }
  }
</script>
