<!--suppress HtmlUnknownTag, HtmlUnknownAttribute, CheckEmptyScriptTag -->
<template>
  <div>
    <v-overlay opacity="0" :id="networkContainerId" />

    <div :style="floatingMenuStyle" >
      <v-btn elevation="2" x-small
             v-if="showCopy"
             @click="onCopy()" >
        <v-icon small>mdi-content-copy</v-icon>
      </v-btn>
      <v-btn elevation="2" x-small
             v-if="showExpand"
             @click="onExpand()" >
        <v-icon small>mdi-arrow-expand</v-icon>
      </v-btn>
      <v-btn elevation="2" x-small
             v-if="showCollapse"
             @click="onCollapse()" >
        <v-icon small>mdi-arrow-collapse</v-icon>
      </v-btn>
    </div>
  </div>
</template>

<script>
  import createVis from '../lib/visImpl.js';

  export default {
    name: 'VisWrapper',
    props: ['data'],

    data: () => ({
      networkContainerId: 'VisNetwork',
      networkContainer: null,

      onCopy: () => {},
      onExpand: () => {},
      onCollapse: () => {},

      floatingMenuLeft: 0,
      floatingMenuTop: 0,

      showCopy: false,
      showExpand: false,
      showCollapse: false
    }),

    computed: {
      floatingMenuStyle: function () { return {
        position: 'absolute',
        left: this.floatingMenuLeft + 'px',
        top: this.floatingMenuTop + 'px',
        opacity: 0.75,
        'z-index': 10
      }}
    },

    methods: {
      setFloatingMenuCoords(left, top) {
        const body = document.body.getBoundingClientRect();
        const rect = this.networkContainer.getBoundingClientRect();

        this.floatingMenuLeft = Math.trunc(rect.left - body.left + left) - 70;
        this.floatingMenuTop = Math.trunc(rect.top - body.top + top) - 50;
      },

      setShowButtons(showCopy, showExpand, showCollapse) {
        this.showCopy = showCopy;
        this.showExpand = showExpand;
        this.showCollapse = showCollapse;
      }
    },

    mounted () {
      this.networkContainer = document.getElementById(this.networkContainerId);
      const callbacks = createVis(this.networkContainer, this.data, this.setFloatingMenuCoords, this.setShowButtons);
      this.onCopy = callbacks.onCopy;
      this.onExpand = callbacks.onExpand;
      this.onCollapse = callbacks.onCollapse;
    }
  }
</script>
