import { Network } from "vis-network/peer";
import { DataSet } from "vis-data/peer";

const options = {
  physics: {
    maxVelocity: 75,
    solver: 'repulsion',
    repulsion: {
      centralGravity: 0.1,
      nodeDistance: 120
    }
  }
};

export default function createVis(networkContainer, inputData, setFloatingMenuCoords, setShowButtons) {

  let selected = null;

  function setSelected(newSelected) {
    selected = newSelected;

    if (network.isCluster(selected) == true)
      setShowButtons(false, true, false);

    else if (keys.indexOf(network.body.nodes[selected].options.meaning) > -1)
      setShowButtons(true, false, true);

    else
      setShowButtons(true, false, false);
  }
  
  function unsetSelected() {
    selected = null;
    setShowButtons(false, false, false);  
  }

  const groups = {};
  const keyedData = {};

  inputData.forEach((d, i) => {
    if (groups[d.meaning])
      groups[d.meaning].push(i);
    else
      groups[d.meaning] = [i];

    if (keyedData[d.phrase] === undefined)
      keyedData[d.phrase] = i;
    else
      console.warn('Duplicate phrase:', d.phrase);
  });

  console.log('Groups before filtering:', groups)
  console.log('Keyed data:', keyedData)

  const nodes = inputData.map((d, i) => ({
    id: i,
    label: '"' + d.phrase + '"\nlit. ' + d.lit + '\nmeaning: ' + d.meaning,
    meaning: d.meaning
  }));
  console.log('Nodes:', nodes)

  const keys = Object.keys(groups).filter(k => groups[k].length > 1);

  const clusterEdges = Object.values(groups).flatMap(g => {
    const s = [];
    for (let i = 0; i < g.length; i++)
      for (let j = i + 1; j < g.length; j++)
        s.push({ from: g[i], to: g[j], width: 20, color: {color: '#888888', opacity: 0.3 }});
    return s;
  });
  console.log('Cluster edges:', clusterEdges)

  const relatedEdges = inputData
    .map((d, i) => [i, d.related])
    .filter(ir => Array.isArray(ir[1]) && ir[1].length > 0)
    .flatMap(ir => ir[1]
      .filter(r => {if (keyedData[r] === undefined) {console.warn('Undefined related phrase referenced:', r); return false;} else return true;})
      .map(r => [ir[0], keyedData[r]].sort()))
    .map(e => ({ from: e[0], to: e[1], color: '#ff0000' }));
  console.log('Related edges:', relatedEdges);

  const edges = [clusterEdges, relatedEdges]
    // TODO Warn about duplicate edge pairs arising
    .flatMap(es => es);
  const network = new Network(networkContainer, { nodes, edges }, options);

  function clusterBy(key) {
    network.clustering.cluster({
      joinCondition: node => node.meaning === key,
      processProperties: (clusterOptions, childNodes, childEdges) =>
        Object.assign(clusterOptions, {
          id: 'cluster: ' + key,
          label: '(' + childNodes.length + ' elements)\nmeaning: ' + key
        })
    });
  }

  // Cluster every meaning with more than 1 element
  keys.forEach(clusterBy);

  network.on("dragStart", params => {
    if (params.nodes.length == 1) 
      setSelected(params.nodes[0]);
  });

  network.on("selectNode", params => {
    if (params.nodes.length == 1)
      setSelected(params.nodes[0]);
    else
      unsetSelected();
  });

  network.on("deselectNode", params => {
    unsetSelected();
  });

  network.on("afterDrawing", ctx => {
    if (selected !== null) {
      const node = network.body.nodes[selected];
      if (node) {
        const xy = network.canvasToDOM(node);
        setFloatingMenuCoords(xy.x, xy.y);
      }
    }
  });

  // Callbacks for the floating buttons
  function onCopy() {
    const textToCopy = network.body.nodes[selected].options.label;

    function cantCopy(e) {
      alert('Could not copy text:\n' + textToCopy + '\n' + e);
    }

    if (navigator && navigator.clipboard && navigator.clipboard.writeText)
      navigator.clipboard.writeText(textToCopy).catch(cantCopy);
    else
      cantCopy('');
  }

  function onExpand() {
    network.openCluster(selected);
    setShowButtons(false, false, false);
  }

  function onCollapse() {
    clusterBy(network.body.nodes[selected].options.meaning);
    setShowButtons(false, false, false);
  }

  return {onCopy, onExpand, onCollapse};
}
